import React from 'react';
import withBem from 'hoc/withBem';
import styled from 'styled-components';
import { useSelector } from 'hooks';

import { CatalogListItem } from '../ListItem/CatalogListItem';

const StyledCatalogList = styled.div`
    margin-left: 112px;
    margin-top: 72px;
    height: 640px;
    overflow: hidden;
`;

const StyledContainer = styled.div`
    display: flex;
    padding: 24px 16px 44px;
    height: 660px;
    box-sizing: border-box;
    overflow-x: scroll;
    --spatial-navigation-contain: contain;
    transition: transform 0.4s ease-in-out;
`;

const Component: React.FC = () => {
    const { itemsMap, order } = useSelector(({ catalogPage }) => catalogPage);
    const items = React.useMemo(() => order.map((itemId) => itemsMap[itemId]), [order, itemsMap]);

    return (
        <StyledCatalogList>
            <StyledContainer>
                {items.map((item) => (
                    <CatalogListItem key={item.guid} {...item} />
                ))}
            </StyledContainer>
        </StyledCatalogList>
    );
};

export const CatalogList = withBem(Component, 'CatalogList');
